import React, { FunctionComponent } from "react";
import { Lesson } from 'ApiModel';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Divider, List, ListItem, ListItemText } from "@material-ui/core";
import { prependOnceListener } from "process";

type HourDialogComponentOwnProps = {
    lessons: Lesson[];
    onClose: (() => void);
    open: boolean;
};

type LessonComponentProps = HourDialogComponentOwnProps;


const HourDialogComponent: FunctionComponent<LessonComponentProps> = ({ lessons, onClose, open }) => {

    const handleClose = () => {
        onClose();
    };

    return <Dialog onClose={handleClose} aria-labelledby="lessons-list-dialog" open={open}>
        <DialogTitle id="lessons-list-dialog-title">Lekcja</DialogTitle>
        <DialogContent>
            <List>
                {lessons.map(lesson => (<>
                    <ListItem key={lesson.subject + lesson.teacher + lesson.color}>
                        <ListItemText
                            primary={`Nazwa lekcji: ${lesson.subject}`}
                            secondary={<>
                                <div>Nauczyciel: {lesson.teacher}</div>
                                <div>Sala: {lesson.classroom}</div>
                            </>}
                        />
                    </ListItem>
                    {/* <Divider variant="inset" component="li" /> */}
                    </>
                ))}
            </List>
        </DialogContent>
        <DialogActions>
            <Button onClick={handleClose} color="primary">Zamknij</Button>
        </DialogActions>
    </Dialog>
};

export default HourDialogComponent;